import Vuex from 'vuex'
import Vue from 'vue'

Vue.use(Vuex)

let baseUrl = (process.env.NODE_ENV === 'production') ? '/mbResume/static' : '/static'

export default new Vuex.Store({
  state: {
    // API Base URL
    baseUrl: baseUrl,
    // Áî®Êà∑‰ø°ÊÅØ
    userInfo: {
      components: ['welcome', 'userinfo', 'end'],
      welcome: {},
      userinfo: {}
    }
  },
  mutations: {
    // Áî®Êà∑‰ø°ÊÅØËÆæÁΩÆ
    userInfoSetter (state, params) {
      for (var key in params) {
        if (params[key] !== null) {
          Vue.set(state.userInfo, key, params[key])
        }
      }
    }
  }
})



